.canvas {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	cursor: crosshair;
}
.imageData {
    display: flex;
    justify-content: space-between;
    margin: 4px 0;
}
.imageDataViewer.loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 25px;
}
.imageData .key {
    margin-right: 15px;
}
.canvasWrapper {
	position: relative;
	flex: 1;
	overflow: hidden;
}

.root {
	display: flex;
	flex-direction: column;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	box-sizing: border-box;
}

.topPart {
	display: flex;
	border-bottom: 2px solid rgba(0, 0, 0, 0.7);
}

.settingsWrapper{
	padding: 2px 7px 7px;
	border-right: 1px solid rgba(0, 0, 0, 0.7);
	user-select: none;
}

.xWrapper {
	border-right: 1px solid rgba(0, 0, 0, 0.7);
	display: flex;
	flex-direction: column;

}

.xPart{
	flex: 1;
	cursor: pointer;
	display: flex;
	align-items: center;
	font-size: 25px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.7);
	padding: 10px;
}

.xPart:hover{
	background: #ccc;
}

.xPart.selected{
	background-color: black;
	color: white;
}

.xPart.selected{
	background-color: black;
	color: white;
	border-bottom-color: white;
}

.xPart:last-child{
	border-bottom: none;
}

.imageDataViewer {
	border-right: 1px solid rgba(0, 0, 0, 0.7);
	padding: 7px;
}

.labelWrapper {
	display: flex;
	justify-content: space-between;
}

.colorPicker {
	margin: 7px;
}

body {
	padding: 0;
	font-size: 13px;
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
		Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

* {
	box-sizing: border-box;
}

.placeholder.canvas {
	background: #ccc;
	color: #888;
	font-weight: bold;
	font-size: 40px;
	text-transform: uppercase;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.dropZone
{
    background: gray;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    z-index: 100;
    opacity: .8;
}
